body {
  margin: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Space Grotesk", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
}

body #root {
  display: flex;
  flex-direction: column;
}

body a {
  text-decoration: none;
  color: unset;
}

body ul,
body li {
  list-style: none;
}

body h2 {
  position: absolute;
  color: white;
  font-size: 36px;
  border-radius: 5px;
  top: 15px;
  left: 70px;
  z-index: 2;
}

@media screen and (max-width: 1400px) {
  body h2 {
    font-size: 28px;
    border-radius: 5px;
    top: 0;
    left: 30px;
    z-index: 2;
  }
  body .skill-section .skill-container {
    flex-direction: column;
  }
  body .main-cards {
    height: 100%;
    min-height: auto;
  }
  body .main-cards .cards-container {
    padding: 100px 0;
    flex-direction: column;
    gap: 20px;
  }
  body .main-cards .cards-container .fa-arrow-right {
    top: 45px;
  }
  body .main-cards .cards-container .fa-arrow-left {
    display: none;
  }
  body .main-cards .cards-container figure:hover {
    min-height: 500px;
  }
}

@media screen and (max-width: 700px) {
  body .banner strong {
    font-size: 18px;
  }
  body .skill-section {
    padding: 7rem 2rem;
  }
  body .skill-section .skill-container {
    flex-direction: column;
  }
  body .skill-section .skill-container article {
    padding: 1rem;
  }
  body .skill-section .skill-container article ul li {
    margin-left: 20px;
    font-size: 16px;
  }
  body .main-cards {
    height: 100%;
    min-height: inherit;
  }
  body .main-cards .cards-container {
    padding: 100px 0;
    flex-direction: column;
    gap: 20px;
  }
  body .main-cards .cards-container .fa-arrow-right {
    top: 45px;
  }
  body .main-cards .cards-container .fa-arrow-left {
    display: none;
  }
  body .main-cards .cards-container figure {
    max-height: 450px;
    max-width: 269px;
  }
  body .main-cards .cards-container figure figcaption h3 {
    font-size: 24px;
    margin-bottom: 5px;
  }
  body .main-cards .cards-container figure figcaption p {
    font-size: 18px;
  }
  body .main-cards .cards-container figure .wrapper img {
    height: 150px;
  }
  body .main-cards .cards-container figure:hover {
    max-height: 500px;
  }
  body .main-cards .cards-container figure:hover .wrapper .skill-card li {
    font-size: 16px;
  }
  body .contact-section {
    padding: 0px 5px;
  }
  body .contact-section form {
    height: auto;
    width: 70%;
    padding: 15px;
    justify-content: space-evenly;
  }
  body .contact-section form label input {
    font-size: 16px;
  }
  body .contact-section form button {
    margin-top: 5px;
    width: 100px;
    border-radius: 15px;
    padding: 10px 20px;
    background-color: #cec7c7;
  }
}

@media screen and (max-width: 408px) {
  body .main-cards .cards-container figure {
    max-height: 450px;
    max-width: 200px;
  }
  body .main-cards .cards-container figure figcaption h3 {
    font-size: 24px;
    margin-bottom: 5px;
  }
  body .main-cards .cards-container figure figcaption p {
    font-size: 18px;
  }
  body .main-cards .cards-container figure .wrapper img {
    height: 150px;
  }
  body .main-cards .cards-container figure:hover {
    max-height: 500px;
  }
  body .main-cards .cards-container figure:hover .wrapper .skill-card li {
    font-size: 16px;
  }
}
